<template>
  <div class="todos" :class="{'is-complete' : todos.completed }">
    <p>
      <input type="checkbox" @change="chang">
      {{todos.title}}
      <button class="del" @click="$emit('deleteEl',todos.id)">x</button>
    </p>
  </div>
</template>

<script>
export default {
  name: "todos",
  props: ["todos"],
  methods: {
    chang: function() {
      //   console.log(todos);
      this.todos.completed = !this.todos.completed;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.todos {
  background-color: #f4f4f4;
  padding: 20px;
  border-bottom: 1px solid red;
}
.is-complete {
  text-decoration: line-through;
}
input{
    cursor: pointer;
}
.del {
  background: #f40;
  color: black;
  border: none;
  padding: 9px 13px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
}
</style>