<template>
  <div class="addtodo">
    <form @submit.prevent="addTodo">
      <input v-model="title" type="text" name="title" placeholder="请添加你的待办事项">
      <input type="submit" value="添加" class="btn">
    </form>
  </div>
</template>

<script>
// import uuid from "uuid";
export default {
  name: "AddTodo",
  props: {},
  data() {
    return {
      title: ""
    };
  },
  methods: {
    addTodo() {
      //   console.log(this.title);
      const newTodo = {
        // id: uuid.v4(),
        title: this.title,
        completed: false
      };
      this.$emit("addtodo", newTodo);
      this.title = "";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 10px;
}
input[type="submit"] {
  /* width: 60px;
    height: 40px; */
  flex: 2;
}
</style>
